'use strict';

/**
* Rayleigh distribution cumulative distribution function (CDF).
*
* @module @stdlib/stats-base-dists-rayleigh-cdf
*
* @example
* import cdf from '@stdlib/stats-base-dists-rayleigh-cdf' ;
*
* var y = cdf( 2.0, 5.0 );
* // returns ~0.077
*
* var myCDF = cdf.factory( 0.5 );
* y = myCDF( 1.0 );
* // returns ~0.865
*
* y = myCDF( 0.5 );
* // returns ~0.393
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import cdf from './cdf.js' ;
import factory from './factory.js' ;


// MAIN //

setReadOnly( cdf, 'factory', factory );


// EXPORTS //

export default cdf;
